
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  line-height: 1.6;
  color: #333;
  overflow-x: hidden;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Navbar Styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(10px);
  padding: 1.2rem 0;
  z-index: 1000;
  transition: all 0.3s ease;
}

.navbar.scrolled {
  background: rgba(0, 0, 0, 0.95);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
}

.navbar .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  color: white;
  text-decoration: none;
  font-size: 1.8rem;
  font-weight: 700;
  letter-spacing: 1px;
  display: flex;
  align-items: center;
  gap: 10px;
  text-transform: uppercase;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 3rem;
  align-items: center;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s;
  font-size: 1rem;
}

.nav-links a:hover {
  color: #d4ff00;
}

.btn-logout, .btn-register {
  background: #d4ff00;
  color: #000;
  border: none;
  padding: 0.7rem 1.8rem;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s;
  font-size: 0.95rem;
}

.btn-logout:hover, .btn-register:hover {
  background: #c0e600;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(212, 255, 0, 0.4);
}

/* Hero Section */
.hero {
  position: relative;
  min-height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url('https://images.unsplash.com/photo-1600596542815-ffad4c1539a9?w=1920') center/cover;
  color: white;
  display: flex;
  align-items: center;
  padding-top: 80px;
}

.hero-content {
  max-width: 800px;
  animation: fadeInUp 1s ease;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero h1 {
  font-size: 4rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  line-height: 1.2;
}

.hero h1 .highlight {
  color: #d4ff00;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 3rem;
  opacity: 0.9;
  max-width: 600px;
}

/* Advanced Search Bar */
.search-bar {
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  padding: 2rem;
  border-radius: 15px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-top: 2rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
}

.search-group {
  display: flex;
  flex-direction: column;
}

.search-group label {
  font-size: 0.85rem;
  color: #d4ff00;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.search-bar select,
.search-bar input {
  padding: 0.9rem;
  border: none;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 1rem;
  font-family: 'Poppins', sans-serif;
  transition: all 0.3s;
}

.search-bar select:focus,
.search-bar input:focus {
  outline: none;
  background: rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 0 2px #d4ff00;
}

.search-bar select option {
  background: #1a1a1a;
  color: white;
}

.search-bar button {
  background: #d4ff00;
  color: #000;
  border: none;
  padding: 1rem 2.5rem;
  cursor: pointer;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  transition: all 0.3s;
}

.search-bar button:hover {
  background: #c0e600;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(212, 255, 0, 0.4);
}

/* Suggestions Pills */
.suggestions {
  margin-top: 2rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  align-items: center;
}

.suggestions span {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.95rem;
}

.suggestion-pill {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 0.5rem 1.2rem;
  border-radius: 20px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s;
}

.suggestion-pill:hover {
  background: rgba(212, 255, 0, 0.2);
  border-color: #d4ff00;
}

/* Partners Section */
.partners {
  padding: 4rem 0;
  background: white;
}

.partners-grid {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  gap: 3rem;
  opacity: 0.5;
  filter: grayscale(100%);
  transition: all 0.3s;
}

.partners-grid:hover {
  opacity: 0.8;
  filter: grayscale(0%);
}

.partners-grid img {
  height: 40px;
  object-fit: contain;
}

/* Features Section */
.features {
  padding: 6rem 0;
  background: #f8f9fa;
}

.features h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #1a1a1a;
}

.features .subtitle {
  text-align: center;
  color: #666;
  margin-bottom: 4rem;
  font-size: 1.1rem;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 3rem;
}

.feature-item {
  background: white;
  padding: 3rem;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
  transition: all 0.3s;
  text-align: center;
}

.feature-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.1);
}

.feature-item svg {
  font-size: 3.5rem;
  color: #d4ff00;
  margin-bottom: 1.5rem;
}

.feature-item h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #1a1a1a;
}

.feature-item p {
  color: #666;
  line-height: 1.8;
}

/* Featured Properties */
.featured-properties {
  padding: 6rem 0;
  background: white;
}

.featured-properties h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #1a1a1a;
}

.featured-properties .subtitle {
  text-align: center;
  color: #666;
  margin-bottom: 4rem;
  font-size: 1.1rem;
}

/* Properties Grid */
.properties-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 2.5rem;
  margin: 3rem 0;
}

.property-card {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s;
}

.property-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.property-image {
  position: relative;
  height: 250px;
  overflow: hidden;
}

.property-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s;
}

.property-card:hover .property-image img {
  transform: scale(1.1);
}

.badge {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 0.6rem 1.2rem;
  border-radius: 25px;
  font-size: 0.85rem;
  font-weight: 600;
  backdrop-filter: blur(10px);
}

.badge.sale {
  background: rgba(39, 174, 96, 0.9);
  color: white;
}

.badge.rent {
  background: rgba(231, 76, 60, 0.9);
  color: white;
}

.property-content {
  padding: 2rem;
}

.property-content h3 {
  margin-bottom: 0.8rem;
  color: #1a1a1a;
  font-size: 1.4rem;
}

.location {
  color: #666;
  display: flex;
  align-items: center;
  gap: 5px;
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
}

.property-specs {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #f0f0f0;
}

.property-specs span {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 0.95rem;
  color: #666;
}

.property-specs svg {
  color: #d4ff00;
}

.property-details {
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
  color: #666;
}

.property-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.price {
  font-size: 1.8rem;
  font-weight: 700;
  color: #1a1a1a;
}

.btn-view {
  background: #1a1a1a;
  color: white;
  padding: 0.8rem 1.8rem;
  text-decoration: none;
  border-radius: 8px;
  transition: all 0.3s;
  font-weight: 500;
}

.btn-view:hover {
  background: #d4ff00;
  color: #000;
  transform: translateX(5px);
}

.btn-view-all {
  display: inline-block;
  text-align: center;
  background: #1a1a1a;
  color: white;
  padding: 1rem 3rem;
  text-decoration: none;
  border-radius: 8px;
  margin: 3rem auto;
  display: block;
  width: fit-content;
  font-weight: 600;
  transition: all 0.3s;
}

.btn-view-all:hover {
  background: #d4ff00;
  color: #000;
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

/* Filter Sidebar */
.properties-page {
  padding: 8rem 0 4rem;
  background: #f8f9fa;
  min-height: 100vh;
}

.properties-page h1 {
  font-size: 2.5rem;
  margin-bottom: 3rem;
  color: #1a1a1a;
}

.properties-layout {
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 3rem;
}

.filter-sidebar {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
  height: fit-content;
  position: sticky;
  top: 100px;
}

.filter-sidebar h3 {
  margin-bottom: 2rem;
  color: #1a1a1a;
  font-size: 1.3rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.7rem;
  font-weight: 600;
  color: #1a1a1a;
  font-size: 0.95rem;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 0.8rem;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s;
  font-family: 'Poppins', sans-serif;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #d4ff00;
}

.price-inputs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.8rem;
}

.btn-filter, .btn-reset {
  width: 100%;
  padding: 1rem;
  margin-top: 0.8rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s;
}

.btn-filter {
  background: #1a1a1a;
  color: white;
}

.btn-filter:hover {
  background: #d4ff00;
  color: #000;
}

.btn-reset {
  background: #f0f0f0;
  color: #666;
}

.btn-reset:hover {
  background: #e0e0e0;
}

/* Property Detail Page */
.property-detail-page {
  padding: 8rem 0 4rem;
  background: #f8f9fa;
}

.image-gallery {
  margin-bottom: 3rem;
  border-radius: 20px;
  overflow: hidden;
}

.main-image {
  width: 100%;
  height: 600px;
  object-fit: cover;
}

.property-details-layout {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 3rem;
}

.main-details {
  background: white;
  padding: 3rem;
  border-radius: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.5rem;
}

.header h1 {
  font-size: 2.2rem;
  color: #1a1a1a;
}

.price-section {
  margin: 2.5rem 0;
  padding: 2rem;
  background: #f8f9fa;
  border-radius: 15px;
}

.price-section .price {
  font-size: 2.5rem;
  color: #1a1a1a;
}

.specs-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1.5rem;
  margin: 2.5rem 0;
}

.spec-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 1.2rem;
  background: #f8f9fa;
  border-radius: 10px;
}

.spec-item svg {
  color: #d4ff00;
  font-size: 1.8rem;
}

.description {
  margin: 3rem 0;
}

.description h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #1a1a1a;
}

.description p {
  color: #666;
  line-height: 1.8;
  font-size: 1.05rem;
}

.property-info {
  margin: 3rem 0;
}

.property-info h3 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: #1a1a1a;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.info-grid div {
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 8px;
}

.features {
  margin: 3rem 0;
}

.features h3 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: #1a1a1a;
}

.features ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  list-style: none;
}

.features li {
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.features li:before {
  content: 'âœ“';
  color: #d4ff00;
  font-weight: bold;
  font-size: 1.2rem;
}

/* Broker Contact Card */
.broker-contact-card {
  background: white;
  padding: 2.5rem;
  border-radius: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
  height: fit-content;
  position: sticky;
  top: 100px;
}

.broker-contact-card h3 {
  margin-bottom: 2rem;
  font-size: 1.5rem;
  color: #1a1a1a;
}

.broker-info h4 {
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  color: #1a1a1a;
}

.company {
  color: #666;
  margin-bottom: 1.5rem;
}

.rating {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 2rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.contact-details {
  margin: 2rem 0;
}

.contact-btn {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 1rem;
  margin-bottom: 1rem;
  background: #f8f9fa;
  border-radius: 10px;
  text-decoration: none;
  color: #333;
  transition: all 0.3s;
  font-weight: 500;
}

.contact-btn:hover {
  background: #d4ff00;
  color: #000;
}

.btn-primary, .btn-secondary {
  width: 100%;
  padding: 1.2rem;
  margin-top: 1rem;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s;
}

.btn-primary {
  background: #1a1a1a;
  color: white;
}

.btn-primary:hover {
  background: #d4ff00;
  color: #000;
}

.btn-secondary {
  background: white;
  color: #1a1a1a;
  border: 2px solid #1a1a1a;
}

.btn-secondary:hover {
  background: #1a1a1a;
  color: white;
}

/* Broker Card */
.broker-card {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
  transition: all 0.3s;
}

.broker-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.broker-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.broker-header h3 {
  font-size: 1.4rem;
  color: #1a1a1a;
}

.specialization {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  margin: 1.5rem 0;
}

.tag {
  background: #d4ff00;
  color: #000;
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
}

.serving-areas {
  margin: 1.5rem 0;
  font-size: 0.95rem;
  color: #666;
}

.broker-contact {
  display: flex;
  gap: 1rem;
  margin: 1.5rem 0;
}

.btn-view-profile {
  display: block;
  text-align: center;
  background: #1a1a1a;
  color: white;
  padding: 1rem;
  text-decoration: none;
  border-radius: 10px;
  margin-top: 1.5rem;
  font-weight: 600;
  transition: all 0.3s;
}

.btn-view-profile:hover {
  background: #d4ff00;
  color: #000;
}

/* Auth Pages */
.auth-page {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #1a1a1a 0%, #333 100%);
  padding: 2rem 0;
}

.auth-container {
  background: white;
  padding: 4rem;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  width: 100%;
  max-width: 500px;
}

.auth-container h2 {
  text-align: center;
  margin-bottom: 2.5rem;
  color: #1a1a1a;
  font-size: 2rem;
}

.error-message {
  background: #e74c3c;
  color: white;
  padding: 1rem;
  border-radius: 10px;
  margin-bottom: 1.5rem;
  text-align: center;
}

.auth-link {
  text-align: center;
  margin-top: 2rem;
  color: #666;
}

.auth-link a {
  color: #1a1a1a;
  text-decoration: none;
  font-weight: 600;
}

.auth-link a:hover {
  color: #d4ff00;
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: center;
  gap: 0.8rem;
  margin: 3rem 0;
}

.pagination button {
  padding: 0.8rem 1.5rem;
  border: 2px solid #e0e0e0;
  background: white;
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.3s;
  font-weight: 600;
}

.pagination button:hover {
  background: #1a1a1a;
  color: white;
  border-color: #1a1a1a;
}

.pagination button.active {
  background: #d4ff00;
  color: #000;
  border-color: #d4ff00;
}

/* Loading & Error States */
.loading, .error {
  text-align: center;
  padding: 4rem;
  font-size: 1.3rem;
  color: #666;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .properties-layout {
    grid-template-columns: 1fr;
  }
  
  .filter-sidebar {
    position: static;
  }
}

@media (max-width: 968px) {
  .property-details-layout {
    grid-template-columns: 1fr;
  }

  .broker-contact-card {
    position: static;
  }

  .hero h1 {
    font-size: 2.5rem;
  }

  .nav-links {
    gap: 1.5rem;
    font-size: 0.9rem;
  }
  
  .search-bar {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .properties-grid {
    grid-template-columns: 1fr;
  }

  .features-grid {
    grid-template-columns: 1fr;
  }

  .info-grid {
    grid-template-columns: 1fr;
  }

  .features ul {
    grid-template-columns: 1fr;
  }

  .property-specs {
    flex-direction: column;
    gap: 0.8rem;
  }
  
  .specs-grid {
    grid-template-columns: 1fr;
  }
  
  .hero h1 {
    font-size: 2rem;
  }
  
  .nav-links {
    display: none;
  }
}
